.logo {
    max-height: 60px;
    object-fit: cover;
}

@media (max-width: 540px) {
    .logo {
        max-height: 40px;
        object-fit: cover;
    }
    .title {
        font-size: .5em;
    }
}

@media (max-width: 320px) {
    .logo {
        max-height: 30px;
        object-fit: cover;
    }
    .title {
        font-size: .4em;
    }
}